import './card.css';
import '../style.css';

export const createCard = ({title, text}) => {
  const card = document.createElement('div');
  const cardInner = document.createElement('div');
  card.className = 'card';
  cardInner.className = 'card-content';
  card.appendChild(cardInner);
  cardInner.appendChild('h1', title);
  cardInner.appendChild('p', text);

  return card;
};
